import Vuex from 'vuex'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import player from '@/components/player/player.vue'
export default {
	data() {
		return {
			banner:[],
			list: [],
			visible: false,
		}
	},
	onLoad() {
		//风格像jq的ajax
		uni.request({
			url: 'https://api.wulv5.com/music/banner',//https://api.wulv5.com/music/personalized/newsong
			success:(res) => {
				this.banner = res.data.banners;
				//console.log(this.banner);
			}
		});
		uni.request({
			url: 'https://api.wulv5.com/music/personalized',//https://api.wulv5.com/music/personalized/newsong
			success:(res) => {
				this.list = res.data.result;
				//console.log(this.list);
			}
		});
		uni.request({
			url: 'https://api.wulv5.com/music/personalized/newsong',//https://api.wulv5.com/music/personalized/newsong
			success:(res) => {
				//console.log(res.data.result);
				[...res.data.result].forEach(val => {
					this.getSrc(val)
				})
			}
		});
	},
	onNavigationBarButtonTap(e) {
		this["top"+e.index]();
	},
	methods: {
		...Vuex.mapActions(['getSrc']),
		top0(){//菜单按钮
			//console.log("这是菜单");
			this.visible = true
		},
		top1(){//搜索按钮
			//console.log("这是搜索");
			uni.navigateTo({
				url: "/pages/search/search"
			})
		},
		closeDrawer(){
			this.visible = false
		},
		tiao() {
			uni.navigateTo({
				url: "/pages/history/history"
			})
		}
	},
	components: {
		uniList,
		uniListItem,
		uniDrawer,
		player
	}
}
